import React from "react";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ExpandLessIcon from "@material-ui/icons/ExpandLess";
function FilterDiv(props) {
  return (
    <div
      style={{
        margin: 10,
        padding: 10,
        boxShadow: "1px 1px 15px rgb(105, 105, 107)",
      }}
    >
      <FormControl component="fieldset">
        <RadioGroup
          aria-label="gender"
          name="gender1"
          value={props.showvalue}
          onChange={(event) => {
            props.showvaluehandleChange(event.target.value);
          }}
        >
          <Grid container>
            <Grid item xs={6}>
              <FormControlLabel
                value="4"
                control={<Radio />}
                label="SHOW ALL"
              />
            </Grid>
            <Grid item xs={6}>
              <FormControlLabel value="1" control={<Radio />} label="WAITING" />
            </Grid>
            <Grid item xs={6}>
              <FormControlLabel value="2" control={<Radio />} label="SUCCESS" />
            </Grid>
            <Grid item xs={6}>
              <FormControlLabel
                value="3"
                control={<Radio />}
                label="CANCELLED"
              />
            </Grid>
          </Grid>
        </RadioGroup>
      </FormControl>
      <Grid container>
        <Grid item xs={12}>
          {props.showmoredivp ? (
            <Button
              onClick={() => {
                props.showmorediv();
              }}
              fullWidth="true"
              variant="contained"
              color="primary"
              endIcon={<ExpandLessIcon />}
            >
              SHOW LESS
            </Button>
          ) : (
            <Button
              onClick={() => {
                props.showmorediv();
              }}
              fullWidth="true"
              variant="contained"
              color="primary"
              endIcon={<ExpandMoreIcon />}
            >
              SHOW MORE
            </Button>
          )}
        </Grid>
      </Grid>
      <br/>
      <Grid container>
        <Grid item xs={12}>
          <span style={{fontSize:20,fontWeight:450,fontFamily:'monospace'}}>Total Earnings :- ₹{props.amount}</span>
        </Grid>
      </Grid>
    </div>
  );
}

export default FilterDiv;
