import React, { useState, useEffect } from "react";
import Axios from "axios";
import Header from "../Header/Header";
import Footer from "../Footer/Footer";
import "./DashBoard.css";
import Itemdiv from "./Itemdiv";
import urlinit from "../ServerAddress";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import MoneyIcon from "@material-ui/icons/Money";
import CancelIcon from "@material-ui/icons/Cancel";
import Referralwindow from "./Referralwindow";
//const urlinit = "http://192.168.43.26:5000";

const DashBoard = (props) => {
  const [data, setdata] = useState([]);
  const [promo, setpromo] = useState("");
  const [uid, setuid] = useState("");
  const [reftab, setreftab] = useState(false);
  const [name, setname] = useState("");

  useEffect(() => {
    getdata();
  }, []);

  const commentsave = (serviceid, comment, formid, username, userid) => {
    Axios.post(urlinit + "/api/postcomments", {
      serviceid: serviceid,
      serviceform: formid,
      userid: userid,
      comment: comment,
      date: new Date().toLocaleString(),
      name: username,
    }).then((res) => {
      getdata();
    });
    // console.log(serviceid,comment,formid)
  };

  const commentupdate = (
    serviceid,
    comment,
    formid,
    username,
    userid,
    commentid
  ) => {
    Axios.post(urlinit + "/api/updatecomments", {
      serviceid: serviceid,
      serviceform: formid,
      userid: userid,
      comment: comment,
      date: new Date().toLocaleString(),
      name: username,
      comid: commentid,
    }).then((res) => {
      getdata();
    });
    // console.log(serviceid,comment,formid)
  };

  const getdata = () => {
    Axios.post(urlinit + "/api/getuserservicedetails", {
      mobno: localStorage.mobno,
    }).then((res) => {
      console.log(res.data);
      setdata(res.data);
      setuid(res.data[0].id);
      setname(res.data[0].name);
      setpromo(res.data[0].uniquepromo);
      console.log('promo--->',res.data[0].uniquepromo);
    });
  };

  const canceljob = (formid) => {
    Axios.post(urlinit + "/api/canceljob", {
      serviceform: formid,
    }).then((res) => {
      getdata();
    });
  };
  const startreferral = () => {
    Axios.post(urlinit + "/api/startreffering", {
      uid: uid,
    }).then((res) => {
      getdata();
    });
  };
  const referral = () => {
    return (
      <div
        style={{
          padding: 10,
          boxShadow: "1px 0px 30px rgb(146, 146, 153)",
        }}
      >
        <Grid container>
          <Grid item xs={12}>
            <Button
              variant="contained"
              onClick={() => {
                setreftab(false);
              }}
              color="secondary"
              endIcon={<CancelIcon />}
            >
              Close
            </Button>
          </Grid>
          <Grid item xs={12}>
            {promo===null ? (
              <Button
                variant="contained"
                onClick={() => {
                  startreferral();
                }}
                color="primary"
                endIcon={<MoneyIcon />}
              >
                Start
              </Button>
            ) : (
              <Referralwindow userid={uid} promo={promo} />
            )}
          </Grid>
        </Grid>
      </div>
    );
  };

  return (
    <>
      <Header />

      <div className="dashboard">
        {reftab ? (
          referral()
        ) : (
          <div
            style={{
              padding: 10,
              boxShadow: "1px 0px 30px rgb(146, 146, 153)",
              display: "flex",
              flexDirection: "row",
            }}
          >
            <Grid container>
              <Grid item xs={12}>
                <span
                  style={{
                    fontSize: 19,
                    fontWeight: 500,
                    
                  }}
                >
                  Welcome! {name}
                </span>
              </Grid>
              <Grid item xs={4}>
                <span style={{ fontSize: 18 }}>Save Money with referral</span>
              </Grid>
              <Grid item xs={4}>
                <Button
                  variant="contained"
                  onClick={() => {
                    setreftab(true);
                  }}
                  color="secondary"
                  endIcon={<MoneyIcon />}
                >
                  Referral
                </Button>
              </Grid>
            </Grid>
          </div>
        )}

        {data
          ? data.map((items, i) => {
              return (
                <Itemdiv
                  key={items.serv}
                  carcompany={items.carcompany}
                  carmodel={items.carmodel}
                  status={items.status}
                  promo={items.promo}
                  finalprice={items.finalprice}
                  date={items.date}
                  serviceid={items.serviceid}
                  address={items.address}
                  sername={items.sername}
                  formid={items.serv}
                  userid={items.id}
                  username={items.name}
                  comment={items.comment}
                  comdate={items.comdate}
                  comid={items.comid}
                  commentfun={commentsave}
                  commentupdate={commentupdate}
                  canceljob={canceljob}
                />
              );
            })
          : null}
      </div>

      <Footer />
    </>
  );
};

export default DashBoard;
