import React from "react";
import "./EarnWithUS.css";
import EarnWithUSPic from "../Addons/earnwithus.jpg";
import Grid from "@material-ui/core/Grid";
import { Link } from "react-router-dom";


const EarnWithUS = () => {
  return (
    <div className="Earnwithus">
      <h1>Earn With-Us</h1>
      <Grid container spacing={3}>
        <Grid item xs={12} sm={6}>
          <span style={{ fontSize: 25, fontWeight: 500 }}>
            Become an Ambassador
          </span>
          <Grid
            container
            direction="column"
            justify="flex-start"
            alignItems="flex-start"
          >
            <ul style={{}}>
              <li>Earn On Every Booking</li>
              <li>Become a Partner</li>
              <li>Boost Your Business</li>
              <li>Promote Your Brand</li>
              <li>Get Exclusive offers</li>
            </ul>
          </Grid>
          <Link className="btearnnow" to="/Earnnowformpage">
          START EARNING NOW &nbsp;₹
                
              </Link>
        </Grid>
        <Grid item xs={12} sm={6}>
          <img style={{ width: "100%" }} src={EarnWithUSPic} />
        </Grid>
      </Grid>
    </div>
  );
};

export default EarnWithUS;
